:root { 
  --black: #000000;
  --mist-gray: #c4c4c4;
  --red: #fb0b0b;
  --roman: #e66161;
  --soapstone: #fff9f9;
  --white: #ffffff;
 
  --font-size-l2: 36px;
  --font-size-m2: 24px;
 
  --font-family-mulish: "Mulish", Helvetica;
}
.mulish-bold-black-24px {
  color: var(--black);
  font-family: var(--font-family-mulish);
  font-size: var(--font-size-m2);
  font-style: normal;
  font-weight: 700;
}

.mulish-bold-white-24px {
  color: var(--white);
  font-family: var(--font-family-mulish);
  font-size: var(--font-size-m2);
  font-style: normal;
  font-weight: 700;
}

.mulish-bold-white-36px {
  color: var(--white);
  font-family: var(--font-family-mulish);
  font-size: var(--font-size-l2);
  font-style: normal;
  font-weight: 700;
}

.mulish-bold-roman-24px {
  color: var(--roman);
  font-family: var(--font-family-mulish);
  font-size: var(--font-size-m2);
  font-style: normal;
  font-weight: 700;
}
